import { useCallback, useEffect, useState, ChangeEvent } from 'react'
import { useDispatch, useSelector } from '../utils/hooks'
import { Link, Redirect } from 'react-router-dom'
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components'
import { responsedEmail, setNewPassword } from '../services/actions/auth'
import { Loading } from '../components/loading/loading'
import styles from './login.module.css'

export const SaveNewPassword = () => {

  const dispatch = useDispatch()
  const { error, isResponsedEmail, isLoading } = useSelector(state => state.auth)

  const [form, setForm] = useState({ password: '', token: '' })

  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value })
  }
  useEffect(() => {
    return () => {
      dispatch(responsedEmail(false))
    }
  }, [dispatch])

  const handleSubmit = useCallback(e => {
    e.preventDefault()
    dispatch(setNewPassword(form))
    setForm({ password: '', token: '' })
  }, [form, dispatch])

  if (!isResponsedEmail) {
    return (
      <Redirect
        to={{
          pathname: '/forgot-password'
        }}
      />
    )
  }
  return (
    <div className={styles.root}>
      {isLoading && <Loading />}
      {error &&
        <p className={`${styles.error} text text_type_main-medium`} >
          {error.message}
        </p>
      }
      <form
        onSubmit={handleSubmit}
        className={`${styles.form} pb-5  mb-5 mt-4`}
      >
        <p className={`${styles.title} text text_type_main-medium mb-5`}>
           
        </p>
        <div className={`${styles.field} mb-5 pt-1`} >
          <Input
            name={'password'}
            type={'password'}
            placeholder={'  '}
            icon={"ShowIcon"}
            onChange={onChange}
            value={form.password}
          />
        </div>
        <div className={`${styles.field} mb-5 pt-1`} >
          <Input
            name={'token'}
            placeholder={'   '}
            onChange={onChange}
            value={form.token}
          />
        </div>
        <div className="pt-1 pb-5 mb-5">
          <Button
            type="primary"
            size="medium"
          >
            
          </Button>
        </div>
      </form>
      <div className={styles.text}>
        <p className="text text_type_main-default mr-2">
           ?
        </p>
        <Link
          className={styles.link}
          to='/login'
        >
          
        </Link>
      </div>
    </div>
  )
}
